$grey: #EFEFEF;
$lightGrey: #FBFBFB;
$textColor: #999999;
$textColorBlack: #1e1e1e;
$colorPrimary: #3369f3;
.chat {
  height: 100%;
  display: grid;
  grid-template-columns: 320px 1fr;
  color: $textColor;
}

.sidebar {

  background-color: $lightGrey;
  height: 100%;
  padding: 20px 10px 0 10px;
  border-right: 1px solid $grey;

  &__profile-btn {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    & p {
      margin-right: .8rem;
    }
  }

  &__search-container {
    display: flex;
    align-items: center;
    margin: 22px 0 14px;
    justify-content: center;
    background-color: $grey;
    padding: 8px 0;
    border-radius: 8px;
  }

  &__chat {
    display: flex;
    padding: 12px 0;
    border-top: 1px solid $grey;

    &-avatar {
      width: 4.7rem;
      height: 4.7rem;
      border-radius: 50%;
      background-color: $grey;
    }

    &-content {
      padding: 0 10rem 0 1.5rem;

      &-name {
        color: $textColorBlack;
        font-weight: 600;
      }
    }

    &-info {
      font-size: 1.1rem;
      margin-left: auto;
      display: flex;
      align-items: center;
      flex-direction: column;

      &-date {

      }

      &-messages-count {
        background-color: $colorPrimary;
        border-radius: 50%;
        padding: 4px;
        width: 1.8rem;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 1.8rem;
        margin-top: auto;
        color: #ffffff;
      }
    }
  }
}

.dialog {
  padding: .5rem 2rem;
  display: grid;
  grid-template-rows: 5.4rem 1fr 5.4rem;

  &__header {
    border-bottom: 1px solid $grey;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-mediabox {
      display: flex;
      align-items: center;
      color: $textColorBlack;
      font-weight: 700;
    }

    &-avatar {
      width: 3.4rem;
      height: 3.4rem;
      border-radius: 50%;
      background-color: $grey;
      margin-right: 1rem;
    }
  }

  &__body {
    padding: 2rem 0;
    color: $textColorBlack;

    &-date {
      text-align: center;
    }

    &-msg-container {
      display: flex;
      margin: 1rem 0;

    }

    &-msg {
      max-width: 50%;
      background-color: #E4EDFD;
      padding: 1rem;
      border-radius: 1rem;
      margin-left: auto;

      &-incoming {
        margin-left: 0;
        background-color: $grey;
      }

      &-time {
        font-size: .8rem;
        text-align: right;
      }
    }
  }

  &__footer {
    border-top: 1px solid $grey;
    padding-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-attach-btn {
    }

    &-input {
      border: none;
      padding: 1.3rem;
      border-radius: 1rem;
      height: 3.2rem;
      width: 90%;
      background-color: $grey;

    }

    &-send-btn {
      background-color: $colorPrimary;
      width: 2.8rem;
      height: 2.8rem;
      border-radius: 50%;
    }
  }
}
